.header {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  background-image: url("../../assets/images/backgroundHeader.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
  position: relative;

  .background {
    background-color: rgba(61, 61, 61, 0.69);
    width: 100%;
    height: 100vh;
    z-index: 2;
    position: relative;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;

    .title {
      font-size: 50px;
      color: white;
      text-shadow: 0px 1px 5px black;
      width: 400px;
      text-align: center;
      animation: k2 20s infinite linear;
      animation-delay: 2s;
    }
  }
}

@keyframes k2 {
  50% {
    color: blue;
    text-shadow: 0px 1px 2px #c7c7c7;
  }
  100% {
    color: white;
    text-shadow: 0px 1px 5px black;
  }
}

@media (max-width: 700px) {
  .header {
    height: 60vh;

    .background {
      height: 60vh;

      .title {
        width: 50%;
        font-size: 25px;
      }
    }
  }
}
