.containerForLinks {
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;

  .link {
    color: white;
    text-decoration: none;
    letter-spacing: 1px;
    transition: 700ms;
    text-shadow: 0px 1px 5px #2f2f2f;

    &:hover {
      cursor: pointer;
      scale: 1.08;
    }
  }
}

@media (max-width: 700px) {
  .containerForLinks {
    display: none;
  }
}
