.form {
  width: 40%;
  margin-top: 40px;
  background-color: #1c1c1c;
  box-shadow: 0px 0px 2px 2px black;
  padding: 20px 20px 40px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 10px;
}
.form .inputEmail, .form .inputSubmit {
  box-sizing: border-box;
  padding: 8px 15px;
  width: 98%;
  background-color: transparent;
  border: 2px solid white;
  color: white;
  font-size: 20px;
  font-weight: bold;
}
.form .inputEmail:hover, .form .inputSubmit:hover {
  border: 2px solid #3acc2f;
}
.form .inputSubmit {
  transition: 500ms;
}
.form .inputSubmit:hover {
  cursor: pointer;
  border: 2px solid #0134e1;
  scale: 0.99;
}
.form .inputSubmit:focus {
  border: 2px solid #02228f;
}

.redBorder:invalid {
  border: 2px solid #ee0b0b;
}

@media (max-width: 550px) {
  .form {
    width: 95%;
    padding: 15px 15px 40px 15px;
  }
  .form .inputEmail, .form .inputSubmit {
    padding: 5px 12px;
    border: 2px solid white;
    font-size: 15px;
  }
}

