.aside {
  background-color: rgba(31, 31, 31, 0.7);
  padding: 10px 20px;
  position: fixed;
  right: 0;
  top: calc(50vh - 80px);
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 10px;

  .icon {
    width: 40px;
    transition: 700ms;

    &:hover {
      cursor: pointer;
      scale: 1.1;
    }
  }
}

@media (max-width: 800px) {
  .aside {
    padding: 5px 10px;
    row-gap: 5px;

    .icon {
      width: 29px;
    }
  }
}

@media (max-width: 700px) {
  .aside {
    display: none;
  }
}
